<template>
  <div class="visual">
    <div class="filter">
      
      <div class="item">
        <label>IP筛选：</label>
        <Select v-model="ipchoice" 
          filterable
          :max-tag-count="1"
          class="select"
          >
            <Option value="ALL">全部</Option>
            <Option v-for="ip in ipfilter" :value="ip" :key="ip">{{ip}}</Option>
        </Select>
      </div>

      <div class="item">
        <label>时间筛选： </label>
        <Select 
          v-model="timefilter"
          class="select">
            <Option value="ALL">全部</Option>
            <Option value="自定义">自定义</Option>
        </Select>
        <div class="selfDefine" v-if="timefilter=='自定义'" >
          <DatePicker v-model="starttime" type="date" placeholder="Select date"></DatePicker>
          <label class="connectLabel"> 至</label>
          <DatePicker v-model="endtime" type="date" placeholder="Select date" ></DatePicker>
        </div>
      </div>
      <div class="item">
        <Button type="primary" icon="ios-funnel" @click="getinfo">筛选</Button>
      </div>

    </div>
    <Table size="large" :columns="title" :data="content">
        <template slot-scope="{ row, index }" slot="handle_preview">
            <Button type="primary" size="small"  @click="preview(index)">预览</Button>
        </template>
    </Table>
    <Modal
        title="数据预览"
        width="90%"
        :footer-hide="true"
        v-model="previewFlag"
        :styles="{'top': '20px'}"
        :mask-closable="false"
      >
        <Table :columns="tableHead" :data="dataPreview"></Table>
      </Modal>

    <div>
      <Page
        class="page"
        size="small"
        :page-size="pagesize"
        show-total
        :total="totalNum"
        :current="storeVisualPage"
        @on-change="pageChange"
      />
    </div>
  </div>
</template>
<style lang="scss">
.filter{
  display: flex;
  .item {
    display:inline-flex;
    align-items: center;
    height: 50px;
    margin-right: 20px;
    .select{
      border: 1px solid rgb(240, 236, 236);
      border-radius: 4px;
    }
  }
}
.selfDefine{
  margin-left: 10px;
  .connectLabel{
    margin-left: 10px;
    margin-right: 10px;
  }
}

</style>

<script>
import api from '@/api/index.js';
import { mapGetters } from 'vuex';
import { Generator } from "@/generator/index";
import { Icon, Select, Table, RadioGroup, Radio, DatePicker, Option,Button, Page, Modal } from "iview";
export default{
  data() {
    return {
      title: [
        { 
            type: 'index',
            title: "序号",
            width: 100,
            align: 'center'
        },
        {
          title: "时间",
          align: 'center',
          key:   "date_created",
        },
        {
          title: "地址",
          align: 'center',
          key:   "ip"
        },
        {
          title: "数据量",
          align: 'center',
          key:   "export_data_number",
        },
        {
          title: "文件格式",
          align: 'center',
          key:   "export_file_type",
        },
        {
          title: "文件名",
          align: 'center',
          key:   "export_filename",
        },
        {
          title: "操作",
          align: 'center',
          width: 150,
          slot:   "handle_preview",
        }
      ],
      content:[],
      timefilter:"ALL",
      ipfilter:[],
      ipchoice:"ALL",
      starttime:'',
      endtime:'',
      totalNum: 0,
      firstvisit: '0',
      pagesize: 10,
      previewDataNum: 10,
      dataPreview: [],
      previewFlag: false,
      tableHead: [],
    };
  },
  components:{
    Table,
    Select,
    RadioGroup,
    Radio,
    Option,
    DatePicker,
    Button,
    Page,
    Modal
  },
  mounted(){
      this.getinfo()
  },
  computed: {
    ...mapGetters(['storeVisualPage'])
  },
  methods: {
      async getinfo(){
        this.content=[];
        var endtime = String(Date.parse(this.endtime));
        var starttime = String(Date.parse(this.starttime));
        if(starttime > endtime){
          this.$Message.error(`起始时间应小于终止时间！`);
          return;
        }
        try {
          const res = await api.getUserRecord(
            {
              'ipchoice': this.ipchoice,
              'timefilter': this.timefilter,
              'starttime': starttime,
              'endtime': endtime,
              'pagesize': this.pagesize,
              'currentpage':this.storeVisualPage,
              'firstvisit':this.firstvisit
          });
        if (res.code === 200) {
          if(this.firstvisit ==='0'){
            this.ipfilter=res.ipfilter;
            this.firstvisit='1';
          }
          this.content=res.data;
          this.totalNum=res.totalNum;

        } else {
          console.error(res);
          this.$Message.error("获取记录信息错误");
        }
      } catch (e) {
        console.error(e);
      }
    },
    // 普通生成
    plainGenerate(configs, nrows) {
      const generator = new Generator(configs, nrows);
      return generator.plainGenerate()
    },
    // ----------  预览
    preview(index){

      this.dataPreview = [];
      try {
        this.dataPreview = this.plainGenerate(JSON.parse(this.content[index].configs), this.previewDataNum);
        // 设置对话框为可见状态
        this.previewFlag = true;
        // 获取数据的所有keys
        const keys = Object.keys(this.dataPreview[0]);
        // 重置数据表头
        this.tableHead = [];
        for(var i = 0; i < keys.length; i++ ){
          this.tableHead.push(
            {
              title: keys[i],
              key:  keys[i]
            }
          )
        };
      } catch (e) {
        this.$Message.error({
          content: e.toString(),
          duration: 5
        });
      }
      
    },
    pageChange(num) {
      this.$store.commit('SET_VISUAL_PAGE', num);
      this.getinfo();
    }
  }
}

</script>